                                 The Geographical Indication –GI tag was given to Temple jewellery from the temple town of Nagercoil. The temple town of Nagercoil derives its name from the Tamil words Nagaraja Koyil meaning Temple of the Serpent King.<br><br>

The Cholas are known for their grand temple structures and the handcrafted jewellery used to adorn the Gods and Goddesses seated in the temples. <br><br>

The designs during those times had opulent large patterns which are seen in the temples at Srirangam in Trichy and Suchindram. This jewelry thus came to be called temple jewellery.<br><br>

Temple Jewellery is a type of ethnic jewellery that features designs inspired by various Gods and Goddesses. Traditionally, it was crafted using gold and adorned with a variety of precious stones and gems, It incorporates precious stones such as rubies, diamonds, kundan, pearls, polkas, Meenakari, and kemp. <br><br>

Both cut and uncut forms of these precious and semi-precious stones are used to add a classic touch to the jewellery.<br><br>

Today a peculiar red and green stone called kuchu kal is now used in this which is got from river beds.<br><br>

This is not only an art but a profession for craftsmen and showcasing it creates awareness and helps preserve this form.<br><br>
